<template>
<div>
	<h3 class="base-title">基础用法</h3>
	<p class="base-content">单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。</p>

	<div class="demo-block" :class="{'show-case-core':caseObj1.showCoreFlag}">
		<div class="demo-block-source">
			<bh-checkbox v-model="checked1" @change='change1'>备选项1</bh-checkbox>::{{checked1}}
			<bh-checkbox v-model="checked2" @change='change2'>备选项2</bh-checkbox>::{{checked2}}
		</div>
		<div class="demo-block-core">
			<textarea class="demo-core-box" readonly="readonly" v-model="caseSourceVal" style='height: 300px;'></textarea>
		</div>
		<div class="demo-block-footer">
			<div class="demo-ms-arr" @click='toggleDemoCore(caseObj1)'>
				<span class="demo-ms-s">显示代码</span>
				<span class="demo-ms-h">隐藏代码</span>
			</div>
			<a :href="caseObj1.coreUrl" target="_blank" class="demo-online" title="前往 codepen.io 运行此示例">在线运行</a>
		</div>
	</div>
</div>
</template>

<script>
const caseSourceVal = `
<template>
<bh-checkbox v-model='checked1' @change='change1'>备选项1</bh-checkbox>::{{checked1}}
<bh-checkbox v-model='checked2' @change='change2'>备选项2</bh-checkbox>::{{checked2}}
</template>
export default {
	data() {
		return {
			checked1: true,
			checked2: false,
		};
	},
	methods: {
		change1(val) {
			console.log('checked1', val);
		},
		change2(val) {
			console.log('checked2', val);
		},
	},
};
`;
export default {
	data () {
		return {
			caseObj1: {
				showCoreFlag: false,
				coreUrl: 'https://codepen.io/xiebaolin/pen/zYdxLdm',
			},
			caseSourceVal: caseSourceVal,

			checked1: true,
			checked2: false,
		};
	},
	methods: {
		// 切换代码显隐
		toggleDemoCore (item) {
			item.showCoreFlag = !item.showCoreFlag;
		},

		change1(val) {
			console.log('checked1', val);
		},
		change2(val) {
			console.log('checked2', val);
		},
	},
}
</script>

